@import url(https://fonts.googleapis.com/css2?family=Rampart+One&family=Poiret+One&family=Press+Start+2P&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{display:flex;flex-direction:column;justify-content:center;align-items:center;background-color:#f63}.App{margin-top:3vh;text-align:center}.App svg{background-color:#f63;border-radius:4px;box-shadow:rgba(0,0,0,0.2) 0px 60px 40px -7px}.App svg .dot{stroke:#252324}.App svg #title{font-family:"Press Start 2P", cursive;fill:#06720f;text-shadow:2px 2px 0 #fff}.App svg #sub-title{font-family:"Press Start 2P", cursive;font-size:0.7em;fill:#fff;text-shadow:1px 1px 0 #06720f;letter-spacing:2px}.App svg #legend text{font-size:0.8em;font-family:"Poiret One", cursive;font-weight:bold;fill:#fff}.App svg line{stroke:#ffcf47}.App svg path{stroke:#ffcf47}.App svg text{fill:#ffcf47;font-weight:650}.App #tooltip{visibility:hidden;width:auto;height:auto;position:absolute;text-align:start;padding:10px;font-size:0.7em;border-radius:10px;font-weight:500}.App #da3ker{color:white;font-size:0.9em;letter-spacing:2px;font-family:"Rampart One", cursive;margin:auto}

